import React, { Component } from 'react'
import { Tabs, Panel, TabsItem, PanelHeader, Div, Button, FixedLayout, ActionSheet, ActionSheetItem} from '@vkontakte/vkui';

import Icon28AddOutline from '@vkontakte/icons/dist/28/add_outline';
import Icon28ViewOutline from '@vkontakte/icons/dist/28/view_outline';
import { render } from '@testing-library/react';

export default class Services extends Component {
    state = {

    }

    render() {
        const { id } = this.props;
        return (
            <Panel id={id}>
                <PanelHeader>Сервисы</PanelHeader>
                <FixedLayout vertical='bottom'>
                    <Div align="center" style={{ paddingBottom:6 }}>
                        <Button className='circle_button'>
                            <Icon28ViewOutline />
                        </Button>
                    </Div>
                </FixedLayout>
            </Panel>
        )
    }
}